/*Crhome Voice Recognition*/
.speechbutton {
  /*display:none;*/
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  border: 0;
  background: transparent;
  opacity: 0;
  cursor: pointer;
}
.speech-button i:before {
  position: relative;
  z-index: 2;
}
.speech-button i:after {
  z-index: 1;
  font-family: 'FontAwesome';
  content: "?";
  font-size: 14px;
  color: #fff;
  position: absolute;
  left: 0;
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  width: 100%;
  opacity: 0;
}
@keyframes blur {
  from {
    font-size: 14px;
    opacity: 1;
    margin-top: 0;
  }
  to {
    font-size: 25px;
    opacity: 0;
    margin-top: -6px;
  }
}
@-webkit-keyframes blur {
  0% {
    font-size: 14px;
    opacity: 1;
    margin-top: 0;
  }
  90% {
    font-size: 25px;
    opacity: 0;
    margin-top: -6px;
  }
  100% {
    font-size: 14px;
  }
}
@-moz-keyframes blur {
  from {
    font-size: 14px;
    opacity: 1;
    margin-top: 0;
  }
  to {
    font-size: 25px;
    opacity: 0;
    margin-top: -6px;
  }
}
@-o-keyframes blur {
  from {
    font-size: 14px;
    opacity: 1;
    margin-top: 0;
  }
  to {
    font-size: 25px;
    opacity: 0;
    margin-top: -6px;
  }
}
.speech-button i.blur:after {
  animation: blur 900ms infinite;
  -webkit-animation: blur 900ms ease infinite;
  -moz-animation: blur 900ms infinite;
  -o-animation: blur 900ms infinite;
}
input::-webkit-input-speech-button {
  -webkit-transform: scale(3, 2.2) translate(-2px, -1px);
  background-color: #fff;
  display: block !important;
}
