  margin-left: 75px;
  padding: 12px 10px;
  border: 1px solid #E8E8E6;
  line-height: 15px;
}
ul.timeline li .content p {
  margin: 0;
}
ul.timeline li .content .img-thumbnail {
  border-radius: 0;
  margin: 10px 5px 0 0;
  cursor: pointer;
}
ul.timeline li .content blockquote {
  margin: 10px 0 0 0;
}
ul.timeline li .content blockquote p {
  margin: 0 0 5px 0 !important;
  font-size: 14px;
}
ul.timeline li .content:before {
  background: #FFF;
  position: absolute;
  top: 14px;
  content: "";
  width: 10px;
  height: 10px;
  left: -6px;
  border-left: 1px solid #E8E8E6;
  border-bottom: 1px solid #E8E8E6;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  /* IE 9 */
  -webkit-transform: rotate(45deg);
  /* Safari and Chrome */
}
ul.timeline li > .date {
  background: #E8E8E8;
  position: absolute;
  top: 49px;
  display: block;
  width: 44px;
  padding: 0 5px;
  text-align: center;
  border: 1px solid #E1E1E1;
  font-size: 11px;
  border-radius: 1px;
  text-shadow: 0 1px 0 #fafafa;
  color: #7B7B7B;
  line-height: 19px;
}
.cbp_tmtimeline {
  margin: 30px 0 0 0;
  padding: 0;
  list-style: none;
  position: relative;
}
/* The line */
.cbp_tmtimeline:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10px;
  background: #9f89cf;
  left: 20%;
  margin-left: -10px;
}
/* The date/time */
.cbp_tmtimeline > li {
  position: relative;
}
.cbp_tmtimeline > li .cbp_tmtime {
  display: block;
  width: 25%;
  padding-right: 100px;
  position: absolute;
}
.cbp_tmtimeline > li .cbp_tmtime span {
  display: block;
  text-align: right;
}
.cbp_tmtimeline > li .cbp_tmtime span:first-child {
  font-size: 0.9em;
  color: #816bb1;
}
.cbp_tmtimeline > li .cbp_tmtime span:last-child {
  font-size: 2.9em;
  color: #a993d9;
}
.cbp_tmtimeline > li:nth-child(odd) .cbp_tmtime span:last-child {
  color: #9f89cf;
}
/* Right content */
.cbp_tmtimeline > li .cbp_tmlabel {
  margin: 0 0 15px 25%;
  background: #9f89cf;
  color: #fff;
  padding: 2em;
  font-size: 1.2em;
  font-weight: 300;
  line-height: 1.4;
  position: relative;
  border-radius: 5px;
}
.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel {
  background: #7761a7;
}
.cbp_tmtimeline > li .cbp_tmlabel h2 {
  margin-top: 0px;
  padding: 0 0 10px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}
/* The triangle */
.cbp_tmtimeline > li .cbp_tmlabel:after {
  right: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-right-color: #9f89cf;
  border-width: 10px;
  top: 10px;
}
.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel:after {
  border-right-color: #7761a7;
}
/* The icons */
.cbp_tmtimeline > li .cbp_tmicon {
  width: 40px;
  height: 40px;
  font-family: 'ecoico';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  font-size: 1.4em;
  line-height: 40px;
  -webkit-font-smoothing: antialiased;
  position: absolute;
  color: #fff;
  background: #7761a7;
  border-radius: 50%;
  box-shadow: 0 0 0 8px #9f89cf;
  text-align: center;
  left: 20%;
  top: 7px;
  margin: 0 0 0 -25px;
}
.cbp_tmicon-phone:before {
  content: "\e000";
}
.cbp_tmicon-screen:before {
  content: "\e001";
}
.cbp_tmicon-mail:before {
  content: "\e002";
}
.cbp_tmicon-earth:before {
  content: "\e003";
}
/* Example Media Queries */
@media screen and (min-width: 1500px) {
  .cbp_tmtimeline > li .cbp_tmtime {
    padding-right: 130px;
  }
}
@media screen and (max-width: 65.375em) {
  .cbp_tmtimeline > li .cbp_tmtime span:last-child {
    font-size: 1.5em;
  }
}
@media screen and (max-width: 47.2em) {
  .cbp_tmtimeline:before {
    display: none;
  }
  .cbp_tmtimeline > li .cbp_tmtime {
    width: 100%;
    position: relative;
    padding: 0 0 20px 0;
  }
  .cbp_tmtimeline > li .cbp_tmtime span {
    text-align: left;
  }
  .cbp_tmtimeline > li .cbp_tmlabel {
    margin: 0 0 30px 0;
    padding: 1em;
    font-weight: 400;
    font-size: 95%;
  }
  .cbp_tmtimeline > li .cbp_tmlabel:after {
    right: auto;
    left: 20px;
    border-right-color: transparent;
    border-bottom-color: #9f89cf;
    top: -20px;
  }
  .cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel:after {
    border-right-color: transparent;
    border-bottom-color: #7761a7;
  }
  .cbp_tmtimeline > li .cbp_tmicon {
    position: relative;
    float: right;
    left: auto;
    margin: -55px 5px 0 0px;
  }
}
